import * as React from "react";
import { graphql } from "gatsby";
import { useTranslation, useI18next } from "gatsby-plugin-react-i18next";
import { Link } from "gatsby";
import Layout from "../components/Layout";
import Helmet from "react-helmet";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";
import Tabs from "react-bootstrap/Tabs";
import Tab from "react-bootstrap/Tab";
import Breadcrumb from "react-bootstrap/Breadcrumb";
import { StaticImage } from "gatsby-plugin-image";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";

const Ampicilina = () => {
  const { t } = useTranslation();
  const i18n = useI18next();
  // const currentLanguage = i18n.language;

  return (
    <Layout>
      <main>
        <Helmet
          htmlAttributes={{
            lang: i18n.language,
          }}
        >
          <title>Health Supply</title>
          <meta
            name="keywords"
            content="medicinas, medicamentos, especialidades, oncologia, neurologia, cardiologia, reumatologia, hepatologia, ecuador"
          />
          <meta
            name="description"
            content="Health Supply, ofrece a pacientes el acceso a medicamentos no disponibles o difíciles de conseguir de forma rápida, segura y cumpliendo todas las normativas legales"
          />
        </Helmet>
        {/* <Menu /> */}
        <Container fluid className="bread-cont bg-light">
          <Row className="bg-light">
            <Col md={12}>
              <Breadcrumb className="pt-2 ps-4 ps-md-5">
                <Breadcrumb.Item>Home</Breadcrumb.Item>
                <Breadcrumb.Item>Productos</Breadcrumb.Item>
                <Breadcrumb.Item>
                  <Link
                    to="/dermatologia"
                    className="text-reset text-decoration-none"
                  >
                    Especidalidad Dermatologia
                  </Link>
                </Breadcrumb.Item>
                <Breadcrumb.Item active>AMPICILINA 500 MG</Breadcrumb.Item>
              </Breadcrumb>
            </Col>
          </Row>

          <Row className="pt-4 pt-md-4 d-flex justify-content-center bg-white">
            <Col md={6} className="my-auto">
              <StaticImage
                alt="health supply mapa"
                src="../images/productos/dermatologia/medicamento2.png"
                className=""
                style={{ height: "100%", width: "100%" }}
                imgStyle={{ objectFit: "contain" }}
              />
            </Col>
            <Col md={5} className="my-auto">
              <h2 className="titulo-home mt-3 mt-md-0">
                AMPICILINA 500 MG CAJA CON 100 CÁPSULAS
              </h2>
              <h2 className="item-price mt-3 mt-md-0">$ 54.450</h2>
              <p className="texto-home mt-4">
                Son pequeños envases solubles generalmente fabricados a base de
                gelatina en cuyo interior se halla la dosis del fármaco que se
                administrará por vía oral.
              </p>
            </Col>
          </Row>
          <Row className="g-4 py-4 py-md-5">
            <Col md={12}>
              <Tabs
                defaultActiveKey="home"
                id="uncontrolled-tab-example"
                className="mb-3"
              >
                <Tab eventKey="home" title="DESCRIPCIÓN">
                  <p>
                    <span className="fw-bold">LAS CAPSULAS: </span>son pequeños
                    envases solubles generalmente fabricados a base de gelatina
                    en cuyo interior se halla la dosis del fármaco que se
                    administrará por vía oral.
                  </p>

                  <p>
                    <span className="fw-bold">LOS COMPRIMIDOS: </span>son formas
                    farmacéuticas sólidas, generalmente redondas, con una
                    dosificación exacta del medicamento contenido en su
                    interior.
                  </p>

                  <p>
                    <span className="fw-bold">LA PASTILLA: </span>es un término
                    coloquial que se le da en a la actualidad a las tabletas y
                    se define como forma farmacéutica sólida de dosificación
                    unitaria, preparadas por moldeo o compresión que contiene
                    principio activo y excipientes.
                  </p>

                  <p>
                    <span className="fw-bold">LAS TABLETAS: </span>son formas
                    farmacéuticas sólidas de dosificación unitaria, obtenidas
                    por compresión mecánica de granulados o de mezclas de polvos
                    con uno o varios principios activos, con la adición, en la
                    mayoría de los casos, de diversos excipientes
                  </p>
                </Tab>
                <Tab eventKey="profile" title="DETALLES - FICHA DE DATOS">
                  <p>
                    Lorem ipsum dolor, sit amet consectetur adipisicing elit.
                    Debitis neque modi beatae ullam? Adipisci, at, recusandae
                    qui ullam placeat nesciunt est aliquid unde expedita odit,
                    odio mollitia sapiente voluptates a.
                  </p>
                </Tab>
                <Tab eventKey="contact" title="DATOS DE CONTACTO">
                  <p>
                    Lorem ipsum dolor sit amet consectetur adipisicing elit.
                    Laboriosam a perferendis incidunt atque tempora rerum magnam
                    exercitationem ab dolore. Ad, ut porro? Labore nostrum cum
                    porro aspernatur quos doloremque rerum.
                  </p>
                </Tab>
              </Tabs>
            </Col>
          </Row>
          <Row className="d-flex justify-content-center py-3 py-md-4 bg-white">
            <Col xs={4} md={2} className="my-auto">
              <figure className="logo-img">
                <StaticImage
                  alt="mhra logo"
                  src="../images/mhra-logo.png"
                  style={{ height: "100%", width: "100%" }}
                  imgStyle={{ objectFit: "contain" }}
                />
              </figure>
            </Col>
            <Col md={1} className="d-none d-sm-block"></Col>
            <Col xs={4} md={2} className="my-auto">
              <figure className="logo-img">
                <StaticImage
                  alt="ema logo"
                  src="../images/ema-logo.png"
                  style={{ height: "100%", width: "100%" }}
                  imgStyle={{ objectFit: "contain" }}
                />
              </figure>
            </Col>
            <Col md={1} className="d-none d-sm-block"></Col>
            <Col xs={4} md={2} className="my-auto">
              <figure className="logo-img">
                <StaticImage
                  alt="fda logo"
                  src="../images/fda-logo.png"
                  style={{ height: "100%", width: "100%" }}
                  imgStyle={{ objectFit: "contain" }}
                />
              </figure>
            </Col>
          </Row>
        </Container>
      </main>
    </Layout>
  );
};

export default Ampicilina;

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(filter: { language: { eq: $language } }) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;
